    },
    watch: {
      isShowAnimation: {
        immediate: true,    // 这句重要
        handler(val) {
          if (val) {
            if (!this.myIsShowAnimation) {
              let pointList = document.getElementsByClassName('map-point')
              let that = this
              Array.prototype.forEach.call(pointList, function (el, index) {
                let delayT = Math.random() * 3
                // init delay for each point
                that.animateGo(delayT, el, that, index)
              })
            }
            this.myIsShowAnimation = true
          } else {
            this.myIsShowAnimation = false
          }
        }
      }
    },
    created() {
      if (!this.isPc) {
        this.pointList = this.pointListMB
      }
    },
    mounted() {
      // for pre-load the img
      Array.prototype.forEach.call(this.pointList, function (el) {
        let image = new Image()
        image.src = el.country
      })

      let that = this
      Array.prototype.forEach.call(document.getElementsByClassName('map-point'), function (el, index) {
        let delayT = Math.random() * 3
        // init delay for each point
        that.animateGo(delayT, el, that, index)
      })
    },
    methods: {
      gotoOrder() {
        this.$router.push({path: 'order/place-order'})
      },
      animateGo(delayT, el, that, index) {
        TweenMax.fromTo(el.getElementsByClassName('pulse')[0],
          (2 - (that.pointList[index].lambda/17)), // calculate the duration with lambda
          {transform: 'scale(0.3)', opacity: '1'}, {css:{transform: 'scale(1.8)', opacity: '0'}, delay:delayT, onComplete: function () {
              // repeat with new delay time
              if (that.myIsShowAnimation)
                that.animateGo(that.getNextTime(that.pointList[index].lambda), el, that, index)
            }})
        // set the second ring with different delay time(1/3 of prev)
        TweenMax.fromTo(el.getElementsByClassName('pulse1')[0], (2 - (that.pointList[index].lambda/17)), {transform: 'scale(0.3)', opacity: '1'}, {css:{transform: 'scale(1.8)', opacity: '0'}, delay:delayT + (2 - (that.pointList[index].lambda/17))/2})
      },
      getNextTime(lambda) {
        // let time = Math.min(-Math.log(1 - Math.random())/(lambda/20), this.maxDelay)
        // console.log(`${lambda} --- ${time}`)
        // calculate the new delay time with Poisson
        return (Math.min(-Math.log(1 - Math.random())/(lambda/20), this.maxDelay))
