<script>
  import lottie from 'lottie-web';
  import connectedJson from '@/assets/json/connected.json';
  import connectedArrowJson from '@/assets/json/connected-arrow.json';

  export default {
    name: 'HomePreviewSession',
    data() {
      return {
        iconList: [
          {
            img: (`${this.$assetsUrl}home/icon-whatsapp.svg`),
          },
          {
            img: (`${this.$assetsUrl}home/icon-gmail.svg`),
          },
          {
            img: (`${this.$assetsUrl}home/icon-netflix.svg`),
          },
          {
            img: (`${this.$assetsUrl}home/icon-telegram.svg`),
          },
          // {
          //   img: require('../assets/home/icon-hulu.svg'),
          // },
          {
            img: (`${this.$assetsUrl}home/icon-youtube.svg`),
          },
          {
            img: (`${this.$assetsUrl}home/icon-facebook.svg`),
          },
          {
            img: (`${this.$assetsUrl}home/icon-ins.svg`),
          },
          {
            img: (`${this.$assetsUrl}home/icon-line.svg`),
          },
          {
            img: (`${this.$assetsUrl}home/icon-twitter.svg`),
          },
        ],
        hideConnectBG: false,
        connectLoading: true,
        clickTime: 0.13,
        animation1: [],
        animation2: [],
        test: false,
        isPc: this.$isPc()
      };
    },
    props: {
      isShowAnimation: true
    },
    watch: {
      isShowAnimation: {
        immediate: true,    // 这句重要
        handler(val) {
          if (!this.animation1 || !this.animation2 || !this.test) {
            return;
          }

          if (val) {
            Array.prototype.forEach.call(this.animation1, function (el) {
              el.resume();
            });
            Array.prototype.forEach.call(this.animation2, function (el) {
              el.resume();
            });
          } else {
            Array.prototype.forEach.call(this.animation1, function (el) {
              el.pause();
            });
            Array.prototype.forEach.call(this.animation2, function (el) {
              el.pause();
            });
          }
        }
      },
    },
    mounted() {
      this.goConnect()
    },
    methods: {
      gotoOrder() {
        this.$router.push({ path: 'order/place-order', name: 'pay-order' });
      },
      goConnect() {
        const that = this;
        TweenMax.fromTo(
          document.getElementById('connect-stand-by'),
          that.clickTime,
          { transform: 'scale(1)' },
          {
            transform: 'scale(0.9)',
            delay: 2,
            onComplete: function () {
              TweenMax.fromTo(
                document.getElementById('connect-stand-by'),
                that.clickTime,
                { transform: 'scale(0.9)' },
                { transform: 'scale(1)' }
              );
            }
          }
        );

        TweenMax.fromTo(
          document.getElementById('connect-stand-by-by'),
          that.clickTime,
          {
            transform: 'scale(1)',
            opacity: 0
          },
          {
            transform: 'scale(0.9)',
            opacity: 0.3,
            delay: 2,
            onComplete: function () {
              TweenMax.fromTo(
                document.getElementById('connect-stand-by-by'),
                that.clickTime,
                {
                  transform: 'scale(0.9)',
                  opacity: 0.3
                },
                {
                  transform: 'scale(1)',
                  opacity: 0,
                  onComplete: function () {
                    that.connectLoading = false;
                    TweenMax.fromTo(
                      document.getElementById('connect-loading'),
                      3,
                      {
                        rotationZ: 0,
                        rotationX: 19
                      },
                      {
                        rotationZ: -(360 * 3),
                        rotationX: 19,
                        ease: Power2.easeInOut,
                        onComplete: function () {
                          that.lottieStart();
                        }
                      });
                  }
                }
              );
            }
          }
        );
      },
      lottieStart() {
        this.connectLoading = true;
        let blueBG = lottie.loadAnimation({
          wrapper: document.getElementById('lottie-centre'),
          animType: 'svg',
          loop: true,
          animationData: connectedJson
        });

        this.hideConnectBG = true;

        let arrow = lottie.loadAnimation({
          wrapper: document.getElementById('lottie-centre-arrow'),
          animType: 'svg',
          loop: false,
          animationData: connectedArrowJson
        });


        let that = this;
        Array.prototype.forEach.call(document.getElementsByClassName('move-block'), function (el, index) {
          TweenMax.set(el, {opacity: 1})
          TweenMax.fromTo(el, 1.5, { rotation: 0 }, {
            css: { rotation: (((index + 1) * 360) / that.iconList.length) },
            delay: (index * 0.02),
            ease: Power1.easeOut,
            onComplete: function () {
              that.test = true;
              that.animation1[index] = TweenMax.fromTo(el, 24, { rotation: (((index + 1) * 360) / that.iconList.length) }, {
                css: { rotation: (((index + 1) * 360) / that.iconList.length) + 360 },
                repeat: -1,
                ease: Power0.easeNone
              });
              that.animation2[index] = TweenMax.fromTo(el.getElementsByClassName('move-icon')[0], 24, { rotation: -(((index + 1) * 360) / that.iconList.length) }, {
                css: { rotation: -(((index + 1) * 360) / that.iconList.length) - 360 },
                repeat: -1,
                ease: Power0.easeNone
              });
            },
            onStart: function () {
              TweenMax.fromTo(el.getElementsByClassName('move-icon')[0], 1.5, { rotation: 0 }, {
                css: { rotation: -(((index + 1) * 360) / that.iconList.length) },
                ease: Power1.easeOut
              });
            }
          });
        });


      }
    }
  };
</script>

