<template>
  <div class="home-item" :class="{'seven-days-container': isPc, 'seven-days-container-mb': !isPc}">
    <div class="content">
      <p class="title">即刻領取 7 天<br v-if="!isPc">免費試用！</p>
      <p class="description">還在猶豫嗎？我們提供 7 天免費試用，保證你喜歡。<br>
        在下面輸入電郵，我們會把試用資訊發到您郵箱！</p>
      <div class="email-input">
        <a-input class="email" placeholder="請輸入您的郵箱" size="large" v-model="email"/>
        <a-button class="use-btn-try" :loading="iconLoading" type="primary" @click="gotoOrder()">立刻試用</a-button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'HomeSevenDaysTry',
    data() {
      return {
        email: '',
        iconLoading: false,
        isPc: this.$isPc()
      }
    },
    methods: {
      gotoOrder() {
        // this.$router.push({path: 'order/place-order'})
        // console.log(this.email.length)
        if (this.email.length <= 0) {
          this.$router.push({
            path: 'signup',
            name: 'users-signup',
            params: {
              email: this.email,
              errorMss: '我們無法識別該郵箱。'
            }
          })
          return
        }
        this.iconLoading = true
        this.$http.post(`${process.env.VUE_APP_SERVER_URL}/api/v2/users`, {
          email: this.email,
          affid: this.$route.query['aff'] || localStorage.getItem('aff') || ''
        })
          .then((data) => {
            this.iconLoading = false;
            // console.log(data)
            if (data.status === 201) {
              this.$router.push({
                path: 'signup-success',
                name: 'signup-success',
                params: {
                  account: this.email,
                  packages: '50G 無限量',
                  cycle: '7天',
                  originalPrice: '3.00',
                  reduce: '3.00',
                  preferentialPrice: '0.00',
                }
              });
            } else {
              let errorMss = ''
              if (data.statusCode === 400) {
                errorMss = '我們無法識別該郵箱。';
              } else {
                if (data.message.indexOf('Email is registered') > -1) {
                  errorMss = '該電子郵件地址已經存在一個用戶。';
                } else if (data.message.indexOf('We do not recognize this domain') > -1) {
                  errorMss = '我們無法識別該郵箱。';
                }else {
                  errorMss = '存在異常，請稍後重試。';
                }
              }

              this.$router.push({
                path: 'signup',
                name: 'users-signup',
                params: {
                  email: this.email,
                  errorMss: errorMss
                }
              })
            }
          });
      }
    }
  };
</script>

